import actionType from './actionType';

const run = (param) => {
    console.log('uiHandler.main', param);
    const {type} = param;
    
    console.log('actionType', type);
    switch(type){
        case actionType.ALERT:
            console.log(param.data);
            alert(param.data);            
        break; 
        case actionType.LINK: 
            window.open(param.url);
        break;
  
        // default:
        //     common.main(type, param)
        // break;
    }
}

export default {
    run,
};