import store from '../vuex/store';
import actionHandler from '../project/actionHandler';
import actionType from '@/dictionary/actionType';

const run = (params) => {
    console.log('actionHandler.run', params);
    const {type} = params;
    switch (type) {
        case actionType.MESSAGE: {
          store.dispatch('SEND_MESSAGE', params.param);
          // store.dispatch('SEND_MESSAGE', { message: intent });
          return;
        }
        case actionType.POPUP_SLIDE: {
          store.commit('POPUP_SLIDE', params.param);
          break;
        }
        case actionType.SAVE: { 
          store.commit('SAVE', params.param);
          break;
        }
        case actionType.LOAD: { 
          store.dispatch('LOAD', params.param);
          break;
        }
        case actionType.CUSTOM: { // 프로젝트 액션
          // this.handleAction(action, param);
          actionHandler.run(params.param);
          break;
        }
     }
};

export default {
    actionType,
    run,
};